*, h1, h2, p {
  margin: 0;
  padding: 0;
}

body {
  padding-left: 40px;
  padding-right: 40px;
  overflow-x: hidden;
}

.select-container {
  margin-top: 20px;
}

.breed-select {
  width: 200px;
  cursor: pointer;
  background-color: #f2f2f2;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  margin-bottom: 10px;
  padding: 8px 20px;
  font-size: 16px;
  position: relative;
}

.breed-select:hover, .breed-select:focus {
  background-color: #ffe6d2;
}

.breed-select option {
  color: #333;
  background-color: #fff;
}

.box {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.cat-info {
  gap: 30px;
  display: flex;
}

.text-container {
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  display: flex;
}

.title, .text, .subtitle {
  color: #22343d;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: normal;
}

.text {
  font-size: 16px;
}

.subtitle {
  font-size: 22px;
  font-weight: 600;
}

.error {
  font-weight: 16px;
  color: red;
  font-weight: 500;
}

.is-hidden {
  display: none;
}

.loader {
  width: 100px;
  height: 100px;
  background: url("cat-background.298efc95.jpg") center / cover;
  border-radius: 50%;
  margin: 200px auto;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

